:root {
  --font-main: "Popins-reg", sans-serif;
  --font-title: "Popins-semb", sans-serif;
  --green-light: #BBFF85;
  --white: #FFFFFF;
  --white-bg: #F3F5F6;
  --black: #0E0C14;
  --black2: #0D0F20;
  --header-input-bg: rgba(154, 94, 245, 0.15);
  --white-opacity: rgba(255, 255, 255, 0.60);
  --purple: #9A5EF5;
  --purple2: #2C0064;
  --purple3: #6202FE;
  --purple4: #48336B;
  --purple-dark: #13012A;
  --purple-gray: #71708D;
  --purple-grad-v1: linear-gradient(180deg, #9A5EF5 0%, #B887FF 100%);
  --purple-grad: linear-gradient(270deg, rgba(72, 51, 107, 0.3) 0%, rgba(40, 29, 59, 0.3) 100%);
  --purple-grad-hover: linear-gradient(270deg, rgba(72, 51, 107, 0.5) 0%, rgba(40, 29, 59, 0.5) 100%);
  --blue-shade: #243A64;
  --blue-gray: #C1C1D4;
  --blue: #1F4AD8;
  --gray: #8E8E98;
  --second-gray: #ECEAF0;
  --red: #C0281C;
}

/* stylelint-disable */
.footer {
  padding: 50px 0 30px 0;
  background: var(--black);
}
.footer__subtext {
  margin: 0 0 60px 0;
}
.footer__subtext p {
  color: var(--white);
  font-size: 15px;
  line-height: 20px;
}
.footer__subtext p:not(:last-child) {
  margin: 0 0 20px 0;
}
.footer__link {
  display: flex;
  align-items: center;
  width: 125px;
  justify-content: center;
  flex-direction: column;
  font-family: var(--font-title);
  font-size: 24px;
  line-height: 24px;
  color: var(--white);
  margin: 0 0 auto 0;
}
.footer__link span {
  color: var(--green-light);
  margin: 10px 0 0 0;
}
.footer__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 0 60px 0;
}
@media screen and (max-width: 1024px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
  }
}
.footer__row {
  display: flex;
  justify-content: space-between;
  width: 60%;
}
.footer__item {
  display: flex;
  flex-direction: column;
  max-width: 270px;
}
.footer__item-title {
  font-family: var(--font-title);
  color: var(--white);
  font-size: 17px;
  line-height: 22px;
  letter-spacing: 0.04em;
  margin: 0 0 20px 0;
}
.footer__item-link {
  display: inline-block;
  font-size: 15px;
  line-height: 20px;
  color: var(--purple-gray);
  transition: color 0.3s ease;
}
.footer__item-link:hover {
  color: var(--white);
}
.footer__item-link:not(:last-child) {
  margin: 0 0 20px 0;
}
.footer__wrap-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto 0 0 0;
}
.footer__social {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 10px 15px;
  margin: 0 0 20px 0;
}
.footer__social-mail {
  display: flex;
  flex-direction: column;
}
.footer__social-mail-text {
  color: var(--white);
  font-size: 17px;
  line-height: 22px;
  margin: 0 0 8px 0;
}
.footer__social-mail-link {
  color: #C1C1D4;
  font-size: 15px;
  line-height: 20px;
  text-decoration: underline;
  transition: color 0.3s ease;
}
.footer__social-mail-link:hover {
  text-decoration: underline;
  color: var(--white);
}
.footer__social-item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 42px;
}
.footer__social-item svg {
  max-width: 100%;
  max-height: 100%;
  fill: var(--white);
}
.footer__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer__sponsor {
  display: flex;
  align-items: center;
  margin: 0 30px 0 0;
}
.footer__sponsor-item {
  display: inline-block;
}
.footer__sponsor-item:not(:last-child) {
  margin: 0 25px 0 0;
}
.footer__text {
  color: #858585;
  font-size: 13px;
  line-height: 20px;
}
.footer__text-link {
  white-space: nowrap;
  color: var(--white);
  text-decoration: underline;
}
.footer__cookie {
  position: fixed;
  width: 100%;
  padding: 20px 40px;
  background: linear-gradient(270deg, #48336B 0%, #281D3B 21.6%, #241B34 76.7%, #48336B 100%);
  z-index: 10;
  bottom: 30px;
  display: flex;
  align-items: center;
}
.footer__cookie .close-cookie {
  cursor: pointer;
  position: absolute;
  right: 18px;
  top: -8px;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid var(--white);
  background: linear-gradient(180deg, #085A3D 0%, #038941 100%);
}
.footer__cookie .close-cookie span {
  position: relative;
  width: 26px;
  height: 26px;
  display: block;
}
.footer__cookie .close-cookie span::before, .footer__cookie .close-cookie span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 2px;
  background-color: var(--white);
}
.footer__cookie .close-cookie span::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.footer__cookie .close-cookie span::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.footer__cookie._hidden {
  display: none;
  visibility: hidden;
}
.footer__cookie-img {
  min-width: 40px;
  max-width: 41px;
  margin: 0 15px 0 0;
}
.footer__cookie-info {
  display: flex;
  align-items: center;
  flex: 1 1 auto;
}
.footer__cookie-text {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.01em;
  color: var(--white);
  flex: 1 1 auto;
  margin: 0 21px 0 0;
}
.footer__cookie-btn {
  display: inline-block;
  padding: 12px 25px 10px;
  white-space: nowrap;
}

@media screen and (max-width: 1600px) {
  .footer__cookie {
    width: 70%;
  }
}
@media screen and (max-width: 1170px) {
  .footer__row {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: center;
    gap: 30px;
  }
  .footer__item {
    justify-self: center;
  }
  .footer__info {
    flex-direction: column;
    align-items: center;
  }
  .footer__sponsor {
    margin: 0 0 25px 0;
  }
  .footer__item:not(:last-child) {
    margin: 0;
  }
}
@media screen and (max-width: 1024px) {
  .footer {
    overflow-x: hidden;
  }
  .footer__item {
    align-items: center;
  }
  .footer__link {
    font-size: 18px;
    line-height: 18px;
  }
}
@media screen and (max-width: 750px) {
  .footer__row {
    display: grid;
    grid-template-columns: auto auto;
    gap: 30px;
  }
  .footer__cookie {
    width: 60%;
  }
}
@media screen and (max-width: 600px) {
  .footer {
    padding: 30px 0;
  }
  .footer__link {
    margin: 0 auto 20px auto;
  }
  .footer__subtext {
    margin: 0 0 40px 0;
  }
  .footer__subtext p {
    font-size: 13px;
    line-height: 15px;
  }
  .footer__wrapper {
    flex-direction: column;
    align-items: stretch;
    margin: 0 0 40px 0;
  }
  .footer__row {
    margin: 0 0 40px 0;
    grid-template-columns: calc(50% - 15px) calc(50% - 15px);
    gap: 30px 15px;
  }
  .footer__item {
    max-width: 100%;
  }
  .footer__social {
    align-self: center;
  }
  .footer__sponsor {
    justify-content: center;
    margin: 0 0 15px 0;
  }
  .footer__sponsor-item:not(:last-child) {
    margin: 0 15px 0 0;
  }
  .footer__text {
    font-size: 11px;
    line-height: 16px;
  }
  .footer__cookie {
    align-items: flex-start;
    padding: 20px 15px;
    bottom: 10px;
    width: 98%;
  }
  .footer__cookie-text {
    font-size: 13px;
    line-height: 22px;
    margin: 0 15px 0 0;
  }
  .footer__cookie-info {
    flex-direction: column;
    align-items: flex-start;
  }
  .footer__btn-top {
    z-index: 10;
    bottom: 29px;
    right: 20px;
    width: 45px;
    height: 43px;
  }
  .footer__btn-top span {
    border-width: 0 7px 9px;
  }
}